import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { NewDownloadCategoryPageComponent } from './new-download-category-page.component';

const routes: Routes = [
  { path: '', component: NewDownloadCategoryPageComponent }
];

@NgModule({
  imports: [
    RouterModule.forChild(routes)
  ],
  exports: [
    RouterModule
  ]
})
export class NewDownloadCategoryPageRoutingModule { }
